import Vue from 'vue';
import Component from 'vue-class-component';
import { Action, State } from 'vuex-class';

import textField from 'muse-ui/textField';

import { Subject } from 'rxjs/Subject';

import Utils from '~/utils';
import { IUserInterface } from '~/entity/user/userInterface';
import { CACHE_NAME, IAddTagConfig } from '~/cache';
import { IAddTagParams } from '~/entity/route_params';
import { Subscription } from 'rxjs/Subscription';
import { ToastMsg } from '~/entity/toast';

interface ITag {
    tag: string;
    selected: boolean;
}

@Component({
    components: {
        'mu-text-field': textField
    }
})
export default class AddTagComp extends Vue {

    // params: IAddTagParams = {
    //     tagType: null
    // };

    tags: ITag[] = null;

    txt: string = null;

    /* config */
    hintText: string = null;

    title: string = null;

    myTags: string[] = null;

    subscription: Subscription = null;

    subject: Subject<string[]> = null;

    isAdd = false;

    maxCount: number = null;

    maxTagLength = 13;

    @Action('showToast')
    showToast;

    get buttonDisabled() {
        if (this.txt && (this.txt.length > 0 && this.txt.length < this.maxTagLength + 1)) {
            return false;
        } else {
            return true;
        }
    }

    async activated () {
        await Utils.initedUser();
        // debugger;
        this.tags = [];

        // const params: IAddTagParams = this['$route'].params;
        // this.params = params;

        const addTagConfig: IAddTagConfig = Utils.getItem(CACHE_NAME.addTagConfig);

        let demoTags = addTagConfig.demoTags;

        const myTags = addTagConfig.myTags;
        this.myTags = myTags;

        // this.subscription = addTagConfig.subscription;
        this.subject = addTagConfig.subject;

        this.subscription = addTagConfig.subscription;

        this.isAdd = addTagConfig.isAdd;

        this.title = addTagConfig.title;

        this.hintText = addTagConfig.hintText;

        this.maxCount = addTagConfig.maxCount;

        // 添加已选标签
        for (const myTag of myTags) {
            this.tags.push({
                tag: myTag,
                selected: true
            });
        }

        // 添加备选标签
        demoTags = demoTags.filter((tag) => {
            for (const myTag of myTags) {
                if (tag === myTag) {
                    return false;
                }
            }
            return true;
        });
        for (const demoTag of demoTags) {
            this.tags.push({
                tag: demoTag,
                selected: false
            });
        }
    }

    deactivated() {
        this.subject = null;
        this.txt = null;
        this.subscription.unsubscribe();
        this.subscription = null;
        this.maxCount = null;
    }

    /**
     * 添加或者删除tag
     * @param tag 
     */
    toggleTag(tag: ITag) {
        const myTags = this.myTags;

        // 检查标签是否超过限制
        if (!tag.selected && this.maxCount !== null) {
            if (myTags.length >= this.maxCount) {
                this.showToast(new ToastMsg('标签不能超过' + this.maxCount + '个'));
                return;
            }
        }

        tag.selected = !tag.selected;
        
        if (tag.selected) {
            // 添加到tags
            myTags.push(tag.tag);
        } else {
            // 去除tag
            myTags.splice(myTags.indexOf(tag.tag), 1);
        }
        this.subject.next(myTags);
    }

    addTag() {
        const txt = this.txt;
        // 检查标签数量是否超过限制
        if (this.maxCount !== null) {
            if (this.myTags.length >= this.maxCount) {
                this.showToast(new ToastMsg('标签不能超过' + this.maxCount + '个'));
                return;
            }
        }

        // 先检查tags 数组中是否已经有这个标签
        for (const tag of this.tags) {
            if (tag.tag === txt) {
                if (!tag.selected) {
                    this.myTags.unshift(tag.tag);
                } 
                tag.selected = true;
                return;
            }
        }
        this.tags.unshift({
            tag: txt,
            selected: true
        });

        this.myTags.unshift(txt);
        this.subject.next(this.myTags);
    }

    handleInputOverflow(over: boolean, maxTagLength: number) {
        if (over) {
            this.txt = this.txt.slice(0, maxTagLength);
        }
    }
}



